@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
	html {
		scroll-behavior: smooth; /* Does NOT work on Safari */
	}

	body {
		font-family: Rubik, sans-serif;
	}

	/*
**  Set Elements Focus Style
*/
	*:focus {
		outline: none;
		border-radius: 10px;
		outline: 2px dashed rgba(34 197 94);
		outline-offset: 6px;
		@apply shadow-lg;
	}

	/*
**  Set Text Selection Style
*/
	::selection {
		@apply text-white;
		@apply bg-green-400;
	}
}

@layer utilities {
	.nav-link {
		@apply inline-block border-b-2 md:border-b-4 p-2 md:p-3 border-transparent hover:border-green-500 hover:text-green-500 transition duration-300;
	}

	.nav-link.active {
		@apply text-green-500  border-green-500;
	}

	.sec--luxury {
		background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
			url(../assets/img/cta/luxury.jpg);
		background-position: center center;
		background-size: cover;
	}

	.sec--cta {
		background: linear-gradient(315deg, #d1fae5 0%, #10b981 74%);
		background-position: center center;
		background-size: cover;
	}

	.error-message {
		display: none;
	}

	input:focus:invalid + .error-message,
	textarea:focus + .error-message {
		display: block;
	}
}
